<template>
  <div class="progress-wrap">
    <svg class="progress-circle svg-content" width="100%" height="100%" viewBox="-1 -1 102 102">
      <defs>
        <linearGradient id="header-shape-gradient" x1="0" y1="0" x2="0" y2="1">
          <stop offset="0%" stop-color="var(--color-stop)" />
          <stop offset="90%" stop-color="var(--color-bot)" />
          <stop offset="100%" stop-color="var(--color-bot)" />
        </linearGradient>

      </defs>
      <path stroke="url(#header-shape-gradient)" d="M50,1 a49,49 0 0,1 0,98 a49,49 0 0,1 0,-98"/>
    </svg>
  </div>
</template>

<script>
import $ from 'jquery';
export default {
  name: "ProgressWrap",
  mounted() {


    //Scroll back to top
    var progressPath = document.querySelector('.progress-wrap path');
    var pathLength = progressPath.getTotalLength();
    progressPath.style.transition = progressPath.style.WebkitTransition = 'none';
    progressPath.style.strokeDasharray = pathLength + ' ' + pathLength;
    progressPath.style.strokeDashoffset = pathLength;
    progressPath.getBoundingClientRect();
    progressPath.style.transition = progressPath.style.WebkitTransition = 'stroke-dashoffset 10ms linear';
    var updateProgress = function () {
      var scroll = $(window).scrollTop();
      var height = $(document).height() - $(window).height();
      var progress = pathLength - (scroll * pathLength / height);
      progressPath.style.strokeDashoffset = progress;
    }
    updateProgress();
    $(window).scroll(updateProgress);
    var offset = 100;
    var duration = 20;
    $(window).on('scroll', function() {
      if ($(this).scrollTop() > offset) {
        $('.progress-wrap').addClass('active-progress');
      } else {
        $('.progress-wrap').removeClass('active-progress');
      }
    });
    $('.progress-wrap').on('click', function(event) {
      event.preventDefault();
      $('html, body').animate({scrollTop: 0}, duration);
      return false;
    });

  }
}
</script>

<style scoped>

.progress-wrap:lang(en) {
  position: fixed;
  right: 25px;
  bottom: 25px;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: block;
  border-radius: 50px;
  box-shadow: inset  0 0 0 2px rgba(0,0,0,0.2);
  z-index: 900;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}
.progress-wrap:lang(ar) {
  position: fixed;
  left: 25px;
  bottom: 25px;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: block;
  border-radius: 50px;
  box-shadow: inset  0 0 0 2px rgba(0,0,0,0.2);
  z-index: 900;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}
.progress-wrap.active-progress {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.progress-wrap::after {
  position: absolute;
  font-family: 'Font Awesome 5 Free';
  content: "\f106";
  text-align: center;
  line-height: 46px;
  font-size: 24px;
  color: var(--primary-color);
  left: 0;
  top: 0;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: block;
  z-index: 1;
  font-weight: 900;
  opacity: 0.5;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;

}
.progress-wrap:hover::after {
  opacity: 1;
  transition: all 200ms linear
}
.progress-wrap::before {
  position: absolute;
  font-family: 'Font Awesome 5 Free';
  content: "\f106";
  text-align: center;
  line-height: 46px;
  font-size: 24px;
  opacity: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  left: 0;
  top: 0;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: block;
  font-weight: 900;
  z-index: 2;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}
.progress-wrap:hover::before ,
.progress-wrap:hover  svg path {
  opacity: 1;
  transition: all 200ms linear;
}
.progress-wrap svg path {
  fill: none;
  opacity: 0.5;
}
.progress-wrap svg.progress-circle path {
  stroke-width: 5;
  box-sizing:border-box;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}
#header-shape-gradient {
  --color-stop: var(--primary-color);
  --color-bot: var(--primary-color);
}

/* End Progress to back up
================================================== */

</style>